<template>
  <div>
    <div class="adiv">
      <van-icon name="arrow-left" class="icon" @click="$router.back()" />
      <h1>登录/注册</h1>
    </div>
 <van-form @submit="onSubmit" v-if="tf" :validate-first="true" ref="form">
  <van-field
    v-model="username"
    name="username"
    label="用户名"
    placeholder="请填写11位手机号码"
    :rules="[{  validator, message: '请填写11位正确号码' }]"
  />
  <van-field
    v-model="password"
    type="password"
    name="password"
    label="密码"
    placeholder="请填写8位登录密码"
    :rules="[{  pattern, message: '请填写8-16位数字加字母登录密码' }]"
  />
  <div style="margin: 16px;">
    <van-button round block type="info" native-type="submit" :color="myColor">登录</van-button>
  </div>
      <p>还没有账号吗，点我去注册呀！<span @click="tf = !tf">注册</span></p>
      <p>有账号忘记密码了吗？那就点我去找回密码呀！<span @click="$router.push('/forget')">找回密码</span></p>


</van-form>
<van-form @submit="onSubmit1" v-else :validate-first="true" ref="form1">
  <van-field
    v-model="username1"
    name="username"
    label="用户名"
    placeholder="请填写11位手机号码"
    :rules="[{  validator, message: '请填写11位正确号码' }]"
  />
  <van-field
    v-model="password1"
    type="password"
    name="password"
    label="密码"
    placeholder="请填写8位登录密码"
    :rules="[{  pattern, message: '请填写8-16位数字加字母登录密码' }]"
  />
    <!-- <van-field
        v-model="yzm"
        type="number"
        name="vercode"
        label="验证码"
        placeholder="验证码"
      />
      <van-tag type="primary" round :color="myColor" class="yzm" @click="yzmfn"
        >获取验证码</van-tag
      > -->
      <van-field
   v-model="yzm"
   name="vercode"
  center
  clearable
  label="短信验证码"
  placeholder="请输入短信验证码"
>
  <template #button>
    <van-button size="small" type="button" class="yzm" @click="yzmfn" :color="myColor">发送验证码</van-button>
  </template>
</van-field>
  <div style="margin: 16px;">
    <van-button round block type="info" native-type="submit" :color="myColor">注册</van-button>
  </div>
      <p>注册好账号了吗，点我去登录呀！<span @click="tf = !tf">登录</span></p>

</van-form>

  </div>
</template>

<script>
import { getyzm,getreg,getlogin} from "../api/home";
// import { getreg} from "../api/home";
// import { getlogin} from "../api/home";

import{mapMutations}from "vuex"

export default {
  name:"login",
  data() {
    return {
      username: '',
      password: '',
      username1: '',
      password1: '',
      yzm:"",
      mycilck:true,
      pattern:/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/,
      tf:true
    };
  },
  methods: {
    ...mapMutations([]),
    validator(val){
        return  /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/.test(val);

  },
  yzmfn(){
    // if(!this.mycilck) return
    // this.$refs.from.validata("username1").catch((err) => {
      
    // }).then((result) => {
      
    // });
    getyzm({mobile:this.username1,type:"register"}).then((result) => {
          console.log("result: ", result);
          this.yzm = result.data.data;
          this.$toast("验证码是" +result.data.data);
          // this.mycilck=false
        })
        .catch((err) => {
          console.log("err: ", err);
        });

  },

    onSubmit(values) {
      console.log('submit', values);
        getlogin(values).then((result) => {
    
         if (result.data.msg == "登录成功") {
          this.$notify({ type: "danger", message: result.data.msg  })
this.$toast(result.data.msg)
  
            this.$router.push("/index");
            sessionStorage.setItem("loginfo", JSON.stringify(result.data));
            
          }

        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    onSubmit1(values) {
      console.log('values: ', values);
    getreg(values).then((result) => {
          console.log("result: ", result);
          this.$toast(result.data.msg);

        })
        .catch((err) => {
          console.log("err: ", err);
        });

    },
  },
};
</script>

<style lang="scss" scoped>
.adiv {
  width: 100vw;
  height: 50px;
  background-color: $mycolor;
  display: flex;
  align-items: center;

  color: aliceblue;
  font-size: 14px;
  margin-bottom: 50px;
}
h1 {
  margin: 0 auto;
  font-size: 16px;
}
.icon {
  font-size: 32px;
}
.btn{
  width: 80vw;font-size: 16px;margin: 0 auto;margin-top: 50px;
}
p{
  width: 100vw;text-align: center; color: #AEAEAE;margin-top: 20px;
}
.yzm{
  float: right;
  margin:20px;
} 
</style>

